import React, {Component} from 'react';
import { FaFacebook, FaEnvelope, FaPhone, FaYoutube } from 'react-icons/lib/fa/';
export default class SoMeButtons extends Component {
  componentDidMount = () => {
    let d = document,
    e = d.documentElement,
    g = d.getElementsByTagName('body')[0],
    x = window.innerWidth || e.clientWidth || g.clientWidth,
    y = window.innerHeight|| e.clientHeight|| g.clientHeight;
    const iconSize = x < 600 ? 18 : 24;
    const filter = count => (count > 0 ? count : "");
  }
  render() {
    const {config} = this.props;
    const url = config.siteUrl;
    const title = config.siteTitle;

    return (
      <ul className="flex center">
        <li><FaEnvelope size={this.iconSize}/></li>
        <li><FaPhone size={this.iconSize}/></li>
        <li><FaFacebook size={this.iconSize}/></li>
        <li><FaYoutube size={this.iconSize}/></li>
      </ul>
    )
  }
}



